.title {
  font-family: 'Manrope', sans-serif;
  font-weight: 700;
  font-size: 32px;
  line-height: 1.4;
  width: 280px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0;
  margin-top: 0;
}

.imgPets {
  position: absolute;
  top: 108px;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: url('../../images/img_mob_1x.png');
  background-repeat: no-repeat;
  background-position: 58% -54px;
  background-size: 475px 457px;
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url('../../images/img_mob_2x.png');
  }
}

@media (min-width: 767px) {
  .title {
    font-size: 68px;
    line-height: 1.5;
    width: 588px;
    margin-left: 32px;
  }

  .imgPets {
    top: 225px;
    min-height: 684px;
    background-image: url('../../images/img_tab_1x.png');
    background-position: 46% -112px;
    background-size: 986px 982px;
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('../../images/img_tab_2x.png');
    }
  }
}

@media (min-width: 1280px) {
  .title {
    width: 525px;
    margin-top: 188px;
    font-weight: 800;
    font-size: 68px;
    line-height: 1.3;
  }
  .imgPets {
    top: 0;
    left: 363px;
    margin-top: 10px;
    background-image: url('../../images/img_desk_1x.png');
    background-position: 50% -104px;
    background-size: 954px 975px;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('../../images/img_desk_2x.png');
    }
  }
}
